html {
  font-family: 'Poppins', sans-serif;
}

body {
  margin-left: 22px;
  margin-right: 22px;
  background-image: url('./background.png');
  background-repeat: no-repeat;
  background-position: center;
  /* background-size: cover; */
}

.main {
  position: relative;
  max-width: 464px;
  margin-right: auto;
  margin-left: auto;
  /* margin-top: 50px;
  margin-bottom: 50px; */
}

.heading {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 54px;
  text-transform: uppercase;
  color: #f2f2f2;
  padding-bottom: 10px;
  margin: 0;
}

.question {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  color: #2f527b;
  margin: 0;
}

.first_question__heading {
  margin-top: 68px;
  margin-bottom: 32px;
  margin-left: 32px;
  margin-right: 59px;
}

.next_qestion {
  padding-top: 68px;
  margin-left: 32px;
  margin-right: 59px;
}

.second_question__heading {
  margin-top: 28px;
  margin-bottom: 32px;
}

.container {
  background: #ffffff;
  border-radius: 24px;
}

.first_question {
  min-height: 515px;
  width: 464px;
}

.second_question {
  min-height: 629px;
  /* width: 464px; */
}

.question-image {
  width: 84px;
  height: 54px;
  object-fit: cover;
}

.result {
  text-align: center;
  height: 542px;
  max-width: 464px;
}

.result_image__container {
  padding-top: 49px;
}

.result_image {
  object-fit: cover;
  width: 238px;
  height: 128px;
}

.result_heading {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 48px;
  line-height: 72px;
  color: #1d355d;
  padding-top: 72px;
  margin: 0;
}

.result_btn {
  border: 2px solid #1d355d;
  box-sizing: border-box;
  border-radius: 12px;
  height: 62px;
  padding-left: 61px;
  padding-right: 61px;
  background-color: white;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
  color: #1d355d;
}

.adventure {
  position: absolute;
  top: -1%;
  left: 65%;
  width: 162px;
  height: 116px;
}

.button_container {
  text-align: center;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.answer_btn {
  text-align: start;
  background: #ffffff;
  width: 400px;
  min-height: 65px;
  border: 2px solid rgba(96, 102, 208, 0.7);
  box-sizing: border-box;
  border-radius: 12px;
  padding-left: 19px;
  padding-right: 19px;
}

.answer_btn:focus {
  outline: none;
  border: none;
}

.btn_value,
.answer_btn {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: rgba(96, 102, 208, 0.8);
}

.btn_value {
  padding-right: 47px;
}

.answer_btn + .answer_btn {
  margin-top: 25px;
}

.correct:hover,
.correct:active,
.correct {
  background-color: #60bf88;
  background-image: url('./assets/correct.svg');
  background-repeat: no-repeat;
  background-position: 95% 50%;
  color: #ffffff;
  border: none;
}

.incorrect:hover,
.incorrect:active,
.incorrect {
  background-color: #ea8282;
  background-image: url('./assets/incorrect.svg');
  background-repeat: no-repeat;
  background-position: 95% 50%;
  color: #ffffff;
  border: none;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

button {
  cursor: pointer;
  border: none;
}

button:hover {
  background: #f9a826;
  border: none;
}

button:hover,
button:focus,
button:focus .btn_value,
button:hover .btn_value {
  color: white;
}

.next {
  text-align: end;
}

.next_button {
  background: #f9a826;
  box-shadow: 0px 2px 4px rgba(252, 168, 47, 0.4);
  border-radius: 12px;
  padding-top: 15px;
  padding-bottom: 14px;
  padding-left: 37px;
  padding-right: 36px;
  font-family: 'Poppins';
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 27px;
  color: #ffffff;
  margin-top: 24px;
  margin-right: 32px;
  margin-bottom: 32px;
}

.letters {
  padding-right: 46px;
}

.result_p {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 27px;
  color: #1d355d;
  padding-bottom: 71px;
  margin-bottom: 0;
}

.result_score {
  color: #60bf88;
  font-size: 32px;
  font-weight: bold;
}

.question_content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
